<!--子组件-->
<template>
<div class="tabbar-item" @click="fn">
<!--slot只能加name属性-->
  <span v-show="iconShow"><slot name="normalIcon"></slot></span>
  <span v-show="!iconShow"><slot name="activeIcon"></slot></span>
  <p v-text="iconText" :class="{active:!iconShow}"></p>
</div>
</template>
<script>
export default{
  data() {
    return {
//      iconShow:true
    }
  },
  props: ['iconText', 'index', 'selected'],
  methods: {
    fn: function (){
//      需要把被点击的index值告诉父组件
      this.$emit('clickIndex',this.index);
    },
//    属性计算，computed定义的变量和data定义的变量很相似 作用是一样的
//    只不过computed的变量 可以通过计算在返回值
  },
  computed: {
    iconShow() {
      if (this.index == this.selected) {
        return false;
      }
      return true;
    }
  }
}
</script>

<style>
.tabbar-item{
  width: 20%;
  height: 70px;
  text-align: center;
  float: left;
}
  .tabbar-item img{
    width: 50%;
  }
.tabbar-item p{
  font-size: 13px;
  vertical-align:top;
  color: #919191;
}
.tabbar-item .active{
  color: #44bf55;
}
</style>



