<template>
 <div>
  <div class="group">
    <Topbar toptext="小组">
      <h2></h2>
      <img src="../../assets/topicon/ic_create_group_chat_blue.png" alt="" slot="img" class="msg1">
      <img src="../../assets/topicon/ic_group_search.png" alt="" slot="img" class="msg2">
    </Topbar>
  </div>
  <Group></Group>
 </div>
</template>
<script>
  import  Topbar from '../../components/topbar.vue'
  import  Group from '../../components/group.vue'
  export default{
    components:{
      Topbar,
      Group
    }
  }
</script>
<style>
  .group{
    text-align: center ;
    /*line-height:40px;*/
    width: 100%;
    background-color: #efefef;
    position: fixed;
    top:0;
    left:0;
    z-index:999;
  }
  .group .msg1{
    position: absolute;
    top:-15px;
    right:-9px;
    transform:scale(.5);
  }
  .group  .msg2{
    position: absolute;
    top:-17px;
    right:43px;
    transform:scale(.5);
  }
</style>



