<template>
  <div>
  <div class="bbb"></div>
  <div class="cont">
    <ul class="yingyin-nav">
      <router-link v-for="item in msg" :to="item.hash" >
        <li v-text="item.text" @click="selectedBook(item.hash)"
            :class="{active:item.hash===selected}">
        </li>
      </router-link>
    </ul>
      <div class="conte">
        <router-view></router-view>
      </div>
  </div>
  </div>
</template>
<script>
    export default{
     data:function(){
       return {
         msg:[
           {
             text:'电影',
             hash:'/bookAudio/file'
           },
           {
             text:'读书',
             hash:'/bookAudio/book'
           },
           {
             text:'电视',
             hash:'/bookAudio/tv'
           },
           {
             text:'同城',
             hash:'/bookAudio/city'
           },
           {
             text:'音乐',
             hash:'/bookAudio/music'
           }
           ],
         selected:'',
       }
     },
      methods:{
       selectedBook(sel){
         this.selected=sel;
       }
      },
   created:function(){
       this.selected=window.location.hash.substr(1)
   }
    }
</script>
<style>
  .bbb{
    width: 100%;
    height: 40px;
  }
.cont{
  width: 100%;
  height: 40px;
  background-color: #fff;
  border-bottom:1px solid #ddd;
}
.cont ul{
  width: 100%;
  height: 100%;
}
  .yingyin-nav{
    border-bottom:2px solid transparent;
  }
  .yingyin-nav .active{
    border-bottom:2px solid #41BF54;
  }
.cont li{
  width: 20%;
  height: 40px;
  text-align: center ;
  line-height:40px;
  float: left;
  color: #5b5b5b;
}
</style>



