<template>
  <div>
    <div id="box">
      <Topbar>
       <img src="../../assets/topicon/ic_scan_gray.png" alt="" slot="img" class="erwei"><input type="text" placeholder="影视 图书 唱片 小组等" slot="inp">
        <img src="../../assets/topicon/ic_chat_white.png" alt="" slot="img" class="msg">
        <img src="../../assets/topicon/ic_group_tab_search.png" alt="" slot="img" class="search">
      </Topbar>
    </div>
    <Content>
    </Content>
  </div>

</template>
<script>
import  Topbar from '../../components/topbar.vue'
import Content from '../../components/content.vue'
export default{
  components:{
    Topbar,
    Content
  }
}
</script>
<style>
#box{
  position: relative;
  width: 100%;
  height: 40px;
  background-color: #44bf55;
  position: fixed;
  top:0;
  left:0;
  z-index:99;
 }

#box  input{
    width: 80%;
    height: 30px;
    margin:4px 0;
    border-radius:10px;
    color: #f3f3f3;
    outline-style:none;
    border:1px solid #ccc;
    text-indent:3rem;
    margin-left:10px;
  }
#box .msg{
   position: absolute;
   top:-22px;
   right:-9px;
   transform:scale(.5);
 }
#box .erwei{
   position: absolute;
   top:-15%;
   right:17%;
   transform:scale(.5);
 }
#box .search{
   position: absolute;
   top:-17px;
  left:-11px;
   transform:scale(.4);
 }
</style>



