<template>
  <div>
  <div class="fm">
    <Topbar toptext="广播">
      <h2></h2>
      <img src="../../assets/topicon/ic_create_group_chat_blue.png" alt="" slot="img" class="msg1">
      <img src="../../assets/topicon/ic_status_search_user.png" alt="" slot="img" class="msg2">
    </Topbar>
  </div>
  <Guang></Guang>
  </div>
</template>
<script>
  import  Topbar from '../../components/topbar.vue'
  import  Guang from '../../components/guangbo.vue'
  export default{
    components:{
      Topbar,
      Guang
    }
  }
</script>
<style>
  .fm{
    text-align: center ;
    line-height:40px;
    width: 100%;
    background-color: #efefef;
    position: fixed;
    top:0;
    left:0;
    z-index:99;
  }
  .fm .msg1{
    position: absolute;
    top:-15px;
    right:-9px;
    transform:scale(.5);
  }
  .fm  .msg2{
    position: absolute;
    top:-21px;
    right:43px;
    transform:scale(.5);
  }
</style>



