<!--父组件-->
<template>
<div class="tabbar-box" >
  <router-link to="/Home">
    <item iconText="首页" @clickIndex="getIndex" index="Home"  :selected="selected">
    <img src="../assets/icon/ic_tab_home_normal.png" slot="normalIcon" >
    <img src="../assets/icon/ic_tab_home_active.png" slot="activeIcon" >
  </item>
  </router-link>
 <router-link to="/bookAudio">
   <item iconText="书影音" index="bookAudio" @clickIndex="getIndex" :selected="selected">
   <img src="../assets/icon/ic_tab_status_normal.png" slot="normalIcon" >
   <img src="../assets/icon/ic_tab_status_active.png" slot="activeIcon" >
 </item>
 </router-link>

  <router-link to="/radio">
    <item iconText="广播" index="radio" @clickIndex="getIndex" :selected="selected">
    <img src="../assets/icon/ic_tab_subject_normal.png" slot="normalIcon" >
    <img src="../assets/icon/ic_tab_subject_active.png" slot="activeIcon" >
  </item>
  </router-link>
  <router-link to="/Group">
    <item iconText="小组" index="Group" @clickIndex="getIndex" :selected="selected">
    <img src="../assets/icon/ic_tab_group_normal.png" slot="normalIcon" >
    <img src="../assets/icon/ic_tab_group_active.png" slot="activeIcon" >
  </item>
  </router-link>

  <router-link to="/mine">
    <item iconText="我的" index="mine" @clickIndex="getIndex" :selected="selected">
    <img src="../assets/icon/ic_tab_profile_normal.png" slot="normalIcon" >
    <img src="../assets/icon/ic_tab_profile_active.png" slot="activeIcon" >
  </item>
  </router-link>
</div>
</template>

<script>
import item from './tabbar-item.vue'
  export default{
  data(){
   return{
//     selected记录锚点值
     selected:''
   }
  },
  components:{
    item
  },
    methods:{
//    clickItem:function(index){
//      this.selected=index;
//      console.log(this.selected)
//    },
      getIndex:function(val){
         this.selected=val;
//         console.log(this.selected);
      }
    },
    created:function(){
      var hash= window.location.hash.split('/')[1]
      if( hash=='#'){
          this.selected='#/Home';
          return;
      }
    this.selected=hash;
    }
  }
</script>

<style>
.tabbar-box{
width: 100%;
height: 60px;
position:fixed;
bottom:-2px;
left:0;
border-top:1px solid #919191;
background-color: #f9f9f9;
}

</style>



