import Vue from 'vue'
import Router from 'vue-router'
 import Home from '../pages/home/home.vue'
 import BookAudio from '../pages/bookAudio/bookAudio.vue'
 import Group from '../pages/Group/Group.vue'
 import Mine from '../pages/mine/mine.vue'
 import Radio from '../pages/radio/radio.vue'

  import Book from '../pages/bookAudio/booktem.vue'
  import City from '../pages/bookAudio/citytem.vue'
  import File from '../pages/bookAudio/filetem.vue'
  import Music from '../pages/bookAudio/musictem.vue'
  import Tv from '../pages/bookAudio/tvtem.vue'
Vue.use(Router)
export default new Router({
  routes:[
    {path:'/home',component:Home},
    {path:'/radio',component:Radio},
    {path:'/Group',component:Group},
    {path:'/Mine',component:Mine},
    {path:'/bookAudio',component:BookAudio,children:[
      {path:'/',component: File},
      {path:'/bookAudio/book',component:Book},
      {path:'/bookAudio/file',component: File},
      {path:'/bookAudio/music',component: Music},
      {path:'/bookAudio/city',component:City},
      {path:'/bookAudio/tv',component:Tv}
    ]}
  ]
})



